.search {
  position: relative;
  margin: auto;
  margin-top: 100px;
  width: 116px;
  height: 116px;
  -webkit-perspective: 1000px;
  perspective: 1000px;
}

.search:not(.s--active) {
  cursor: pointer;
}

.search.s--hidden {
  opacity: 0;
}

.search.s--cloned {
  position: absolute;
  left: 50%;
  top: 50%;
  right: auto;
  bottom: auto;
  margin-left: -58px;
  margin-top: -58px;
  margin-right: 0;
  margin-bottom: 0;
}

.search.s--moving {
  -webkit-transition: -webkit-transform 0.4s
    cubic-bezier(0.56, -0.49, 0.58, 0.9);
  transition: -webkit-transform 0.4s cubic-bezier(0.56, -0.49, 0.58, 0.9);
  transition: transform 0.4s cubic-bezier(0.56, -0.49, 0.58, 0.9);
  transition: transform 0.4s cubic-bezier(0.56, -0.49, 0.58, 0.9),
    -webkit-transform 0.4s cubic-bezier(0.56, -0.49, 0.58, 0.9);
}

.search__bg {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: #f63;
  -webkit-transition: all 0.6s ease;
  transition: all 0.6s ease;
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
}

.search.s--active .search__bg {
  -webkit-transform: translate3d(-50%, -50%, 0) scale(48);
  transform: translate3d(-50%, -50%, 0) scale(48);
}

.search__box {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 50px;
  height: 50px;
  border: 4px solid #fff;
  border-radius: 25px;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-transition: all 0.45s ease;
  transition: all 0.45s ease;
}

.search.s--active .search__box {
  width: 500px;
  height: 100px;
  border-radius: 50px;
}

.search__input {
  background: transparent;
  border: none;
  outline: none;
  pointer-events: none;
  opacity: 0;
  color: #fff;
  font-size: 40px;
  -webkit-transition: opacity 0s;
  transition: opacity 0s;
}
.search__input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.4);
}
.search__input::-moz-placeholder {
  color: rgba(255, 255, 255, 0.4);
}
.search__input:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.4);
}
.search__input::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.search.s--active .search__input {
  padding: 23px 90px 23px 40px;
  width: 370px;
  height: 55px;
  pointer-events: auto;
  opacity: 1;
  -webkit-transition: opacity 0.18s 0.48s;
  transition: opacity 0.18s 0.48s;
}

.search__line {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 20px;
  height: 4px;
  border-radius: 2px;
  background: #fff;
  -webkit-transform-origin: 0 50%;
  transform-origin: 0 50%;
  -webkit-transform: translate(14px, 14px) rotate(45deg);
  transform: translate(14px, 14px) rotate(45deg);
  -webkit-transition: all 0.48s;
  transition: all 0.48s;
}

.search__line:before {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: inherit;
  height: inherit;
  background: inherit;
  border-radius: inherit;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  opacity: 0;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}

.search.s--active .search__line:before {
  opacity: 1;
  -webkit-transition: opacity 0.3s 0.42s;
  transition: opacity 0.3s 0.42s;
}

.search.s--active .search__line {
  width: 36px;
  height: 6px;
  -webkit-transform: translate(180px, 11px) rotate(-45deg);
  transform: translate(178px, 8px) rotate(-45deg);
}

.search__close {
  position: absolute;
  right: 0;
  top: 0;
  width: 30px;
  height: 30px;
  pointer-events: none;
}

.search.s--active .search__close {
  right: 44px;
  top: 33px;
  pointer-events: auto;
  cursor: pointer;
}

.search-overlay {
  z-index: -1000;
  overflow: hidden;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.search-overlay.s--active {
  z-index: 9999;
}
